@import "../../../variables";

.button {
    border: 1px solid rgba(255, 255, 255, 0.7);
    padding: 0.5rem 1rem;
    border-radius: 30px;
    background-color: transparent;
    outline: none;
    font-size: 18px;
    margin-right: 1rem;
    color: $gray;
    cursor: pointer;
    display: inline-flex;
    
    align-items: center;

    >i {
        font-size: 24px;
        margin-right: 0.5rem;
    }
}